            <label class="label"><span class="compulsion">*</span></label>
            <div class="row mx-0 text-center py-2 font-weight-bold">
                <div class="col-6">
                    <span id="gender_0" class="border rounded-pill px-4 py-2 gender" @click="genderSelect(0)"></span>
                </div>
                <div class="col-6">
                    <span id="gender_1" class="border rounded-pill px-4 py-2 gender" @click="genderSelect(1)"></span>
                </div>
            </div>
        </div>
        <div class="age py-3">
            <label class="label"><span class="compulsion">*</span></label>
            <div class="row small text-center font-weight-bold">
                <div class="col-4">
                    <div id="age_0" class="border py-2 px-1  rounded-pill my-2" @click="ageSelect(0)">10</div>
                </div>
                <div class="col-4">
                    <div id="age_1" class="border py-2 px-1  rounded-pill my-2" @click="ageSelect(1)">10</div>
                </div>
                <div class="col-4">
                    <div id="age_2" class="border py-2 px-1  rounded-pill my-2" @click="ageSelect(2)">20</div>
                </div>
                <div class="col-4">
                    <div id="age_3" class="border py-2 px-1  rounded-pill my-2" @click="ageSelect(3)">30</div>
                </div>
