                    <div id="age_4" class="border py-2 px-1  rounded-pill my-2" @click="ageSelect(4)">40</div>
                </div>
                <div class="col-4">
                    <div id="age_5" class="border py-2 px-1  rounded-pill my-2" @click="ageSelect(5)">50</div>
                </div>
