</template>

<script>
import $ from 'jquery';
import axios from 'axios'
axios.defaults.headers.post["Content-Type"] = "application/x-www-form-urlencoded";
export default {
    data() {
        return {
            user_name: "",
            genderSelected: false,
            ageSelected: false,
            checked: false,
            gender: 0,
            age: 0,
        }
    },
    watch: {
        genderSelected() {
            this.firstViewCheck();
        },
        ageSelected() {
            this.firstViewCheck();
        },
        checked() {
            this.firstViewCheck();
        }
    },
    mounted() {
        $('.page-top').width($(window).width()).height($(window).height());
        $('.top-image-wrapper > img').height($(window).height() - 160);
        $('body').css('overflow', 'hidden');
        $('body').css('position', 'fixed');
    },
    methods: {
        firstViewCheck() {
            if (this.genderSelected && this.ageSelected && this.checked) {
                $('#start_btn').prop('disabled', false);
            } else {
                $('#start_btn').prop('disabled', true);
            }
        },
        start() {
            if (this.user_name != '') {
                this.$parent.userInfoArr.name = this.user_name;
            }
            $('body').css('overflow', 'auto');
            $('body').css('position', 'static');
            this.$parent.isFirstView = false;
        },
        ageSelect(age) {
            this.age = age;
            this.ageSelected = true;
            for (var i = 0; i < 7; i++) {
                if (i == age) {
                    $('#age_' + i).css({
                        "background": "#74b9ff",
                        "color": "white"
                    })
                } else {
                    $('#age_' + i).css({
                        "background": "#2d3436",
                        "color": "white"
                    })
                }
            }
        },
        genderSelect(gender) {
            this.gender = gender;
            this.genderSelected = true;
            if (gender == 0) {
                $('#gender_0').css({
                    "background": "#0984e3",
                    "color": "white"
                })
                $('#gender_1').css({
                    "background": "#2d3436",
                    "color": "white"
                })
            } else {
                $('#gender_0').css({
                    "background": "#2d3436",
                    "color": "white"
                })
                $('#gender_1').css({
                    "background": "#ff7675",
                    "color": "white"
                })
            }
        },
    },
    computed: {
        imageLoad: function () {
            return function (fileName) {
                return require('../assets/images/' + fileName + '.jpg');
            }
        },
    }
}
</script>

